.m-main__wrap {
  background-color: #0cc2f0;
  height: 100vh; }

.m-main__btn {
  position: fixed;
  right: 50px;
  top: 50%;
  display: block;
  width: 100px;
  transform: translateY(-50%);
  height: 90px;
  border-radius: 16px;
  background-color: #0cc2f0;
  text-align: center;
  border: 1px solid rgba(255, 255, 255, 0.4); }
  @media (max-width: 1239px) {
    .m-main__btn {
      width: 80px;
      height: 60px;
      right: 10px; } }

.top {
  margin-top: 60px; }
  .top__list {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    margin: 0; }
    @media (max-width: 1239px) {
      .top__list {
        padding: 0; } }
  .top__item {
    padding: 17px 0;
    text-align: center;
    color: #fd0425;
    font-weight: bold;
    font-size: 30px;
    background-color: #facd06;
    text-shadow: 2px 2px 4px #302f2f;
    letter-spacing: 0.2em;
    border: 1px solid #7a6a6a;
    transition: .5s;
    font-family: 'Philosopher', serif; }
    .top__item:hover {
      background-color: #e4e729;
      color: #2c09f5; }
    @media (max-width: 1239px) {
      .top__item {
        font-weight: bold;
        font-size: 20px;
        padding: 16px 0; } }

.block {
  position: relative;
  height: 100%;
  z-index: 3;
  display: flex;
  justify-content: center; }
  .block__wrap {
    position: absolute;
    left: 0;
    right: 0;
    top: 50%;
    transform: translateY(-50%); }
  .block--two {
    position: relative; }

.buttons {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  grid-template-rows: repeat(7, 1fr);
  margin: 0;
  position: absolute;
  top: 0;
  width: 100%;
  bottom: 0%;
  z-index: 3; }

.block-bg {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  bottom: 0;
  z-index: 1; }
  .block-bg__cover {
    padding-bottom: 53.2%; }
  .block-bg__image {
    position: absolute;
    top: 0;
    width: 100%;
    bottom: 0; }

.button {
  font-size: 2em;
  color: #060afa;
  text-shadow: 2px 2px 6px 6px #000;
  background-color: #05fd05;
  border: 1px solid #060afa;
  cursor: pointer; }
  .button:hover {
    background: linear-gradient(to left, #eeff01, #00ffbf, #eeff01);
    color: red; }
  .button__color {
    background-color: transparent; }

.link {
  display: block;
  position: absolute;
  top: 0;
  width: 100%;
  bottom: 22.8%; }

.question {
  border: 2px solid #f8eb7a;
  border-radius: 20px;
  padding: 30px 40px;
  color: #fff;
  font-size: 40px;
  text-shadow: 3px 3px 6px #302f2f;
  letter-spacing: 0.1em;
  background-color: #2846ee;
  line-height: 1em;
  box-shadow: 8px 8px 18px #000; }
  @media (max-width: 1239px) {
    .question {
      font-size: 1.6em;
      line-height: 1.3em;
      padding: 20px 30px; } }

.answers__wrap {
  margin-top: 60px;
  display: grid;
  grid-template-columns: 1fr 1fr; }

.answer {
  position: relative;
  border-radius: 20px;
  margin: 18px 10px;
  padding: 30px 20px 30px 30px;
  font-size: 40px;
  color: #fff;
  letter-spacing: 0.1em;
  line-height: 1.2em;
  text-shadow: 3px 3px 6px #302f2f;
  background-color: #2846ee;
  box-shadow: 8px 8px 18px #000;
  text-align: left;
  font-family: 'Philosopher', serif; }
  @media (max-width: 1239px) {
    .answer {
      font-size: 1.6em;
      line-height: 1.3em;
      padding: 20px 20px 30px 70px; } }
  .answer:active {
    box-shadow: 1px 1px 4px 0 #000; }
